import React, { Component } from 'react'
import ScrollAnimation from 'react-animate-on-scroll'
import 'animate.css/animate.min.css'

import styles from './ProjectsSection.module.css'

import { dataSet } from './constants'
import ProjectCards from './Components/ProjectCards'

export default class ProjectsSection extends Component {
    render() {
        return(
            <div className={styles.container}>
                <div className={styles.headingSection}>
                    <ScrollAnimation animateIn="fadeInUp" duration={0.7}>
                        <span role="img" aria-label="Laptop Icon" className={styles.laptopIcon}>💻</span>
                        <div className={styles.headingText}>
                            <b>Our Projects</b>
                        </div>
                        <div className={styles.descText}>
                            List of projects undertaken in Bits Pilani Hyderabad Campus
                        </div>
                    </ScrollAnimation>
                </div>
                <ScrollAnimation animateIn="fadeInUp" duration={0.7}>
                    <div className={styles.projectListSection}>
                        {dataSet.map(dataItem => {
                            return (
                                <ProjectCards
                                    data={dataItem}
                                />
                            )
                        })}
                    </div>
                </ScrollAnimation>
            </div>
        )
    }
}